    letter-spacing: 8.6px;
}

.sub-title {
    padding: 10px 0 10px;
    text-align: center;
    font-style: italic;
    font-family:'Georgia';
}

.myself-home {
    display: flex;
    flex-direction: row;
}


.left-side {
    width: 25%;
    background-color:#f1f1f1;
}

.left-side img {
    display: flex;
    width: 50%;
