    /* align-items: center; */
}

.left-side h5 {
    letter-spacing: 4px;
    margin-top: 35px;
    font-weight: 600;
    text-align: center;
    font-family:'Georgia';
}

.left-side h6 {
    text-align: right;
    margin-right: 30px;
    font-family: Georgia, 'Times New Roman', Times, serif;
    font-size: 17px;
    font-weight: 500;
}

.left-side p {
    font-size: 20px;
    text-align: center;
    font-family: Georgia, 'Times New Roman', Times, serif;
}

.skill-bar {
    width: 660px;
    margin-bottom: 30px;
}

.skill-bar p {
    font-size: 18px;
    display: flex;
    margin: 0px 0 0px 10px;
    text-align: center;

}

.right-side {
