.home-button {
    display: flex;
    justify-content: center;
}

.home-button button {
    width: 130px;
    height: 55px;
    border: none;
    border-radius: 5%;
    background-color: #bcff95;
    font-family: monospace;
    position:relative;
    margin: 30px 0 0 600px;
    box-shadow: 0px 8px 15px rgba(0,0,0,0.1);
    transition: all 0.5s ease 0s;
    cursor: pointer;
}

.home-button button:hover {
    background-color: #35b17f;
    box-shadow: 0px 15px 20px rgba(46, 229, 157, 0.4);
    color: #fff;
    -webkit-transform: translateY(-8px);
            transform: translateY(-8px);
}

.right-side button p {
    font-size: 16px;
    padding: 0 0px 0 0px;
    position:relative;
}

.home-icon {
    font-size: 200%;
    position: absolute;
    margin: 0px 0px 0 -50px;
    /* margin-right: 500px; */
    /* position: absolute; */
}

.progressbar-container{
    height: 23px;
    width: 20vw;
    background-color: #e0e0de;
    border-radius: 50px;
    /* justify-content: left; */
    display: flex;
    margin: 5px auto 0 30px;
    box-shadow: inset 0 1px 2px rgba(0,0,0,0.25);
    /* position: absolute; */
}

.progressbar-inline {
    height: 100%;
    border-radius: 20px 0 0 20px;
    overflow: hidden;
    /* display: inline-block; */
    /* vertical-align: middle; */
    background-size: 100%;
    text-align: inherit;
    font-family:'Georgia';
    transition: width 1s ease-in-out;
}

.progressbar-inline::after {

}

.progressbar-label {
    padding-left: 10px;
    /* margin: 20; */
    /* left: 20px; */
    color: white;
    /* font-weight: bold; */
    font-size: 15px;
}




.contact {
    background: linear-gradient(rgba(248, 255, 145, 0.623),transparent);
    background-color: rgba(66, 192, 250, 0.904);
    display: flex;
    justify-content: center;
    width: 100vw;
    height: 100vh;
    
}

.contact-title {
    text-align: center;
    font-size: 100px;
    margin: 38px 0 30px 0 ;
    position:static;
    font-family: 'copperplate';
}

.contact-container {
    position:fixed;
    margin: 190px auto;
    width: 700px;
    background: white;
    padding: 30px;
    /* text-align: left; */
    border-radius: 20px;
    height: 590px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.contact-container label {
    padding: 20px 30px 0px 30px;
    position: absolute;
    font-size: 20px;
    font-family: 'copperplate';
}

.contact-container input[type=text] {
    border: 1.8px solid #ccc;
    width: 60%;
    border-radius: 2px;
    padding: 12px 20px;
    margin: 3px 0 0 195px;
    box-sizing: border-box;
    align-items: center;
    display:flex;
    /* position:relative; */
}

.message {
    width: 430px;
    height: 200px;
    margin: 30px 0 0 150px;
    border: 1.8px solid #ccc;
}

.contact-container button {
    width: 200px;
    height: 50px;
    border: none;
    margin: 10px 0 0 220px;
    background-color: rgb(52, 209, 31);
    font-family: 'copperplate';
    color: white;
    border-radius: 3%;
    font-size: 26px;
}

.contact-container button:hover {
    background-color: rgb(95, 218, 25);
}



.card {
    font-family: 'Open Sans', sans-serif;
    overflow: hidden;
    box-shadow: 0 2px 20px rgb(202, 199, 199);
    border-radius: 0.2rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    cursor: pointer;
    transition:  200ms ease-in;
    margin-top: 40px;
    margin-bottom: 50px;
}

.card:hover {
    -webkit-transform: scale(1.1) ;
            transform: scale(1.1) ;
}

.card-image {
    height: 12rem;
    width: 100%;
    object-fit: cover;
}

.card-title-lang {
    /* display: flex; */
    /* flex-direction: column; */

}

.card-title {
    padding: 1rem;
    margin: 0px 6px 0 0;
    float: left;
}

.card-lang {
    padding: 1.3rem;
}

.card-description {
    padding-left: 1rem;
}

.card-button {
    width: 230px;
    height: 50px;
    padding: 0;
    font-family: inherit;
    font-weight: bold;
    font-size: 1rem;
    margin: 10px 0 10px 85px;
    border: 2px solid rgb(130, 202, 88);
    background: transparent;
    color: rgb(130, 202, 88);
    border-radius: 0.2rem;
    transition: background 200ms ease-in, color 200ms ease-in;
}

.card-button:hover {
    -webkit-transform: scale(1.02);
            transform: scale(1.02);
    background-color: rgb(130, 202, 88);
    color: white;
}

.portfolio {
    background: linear-gradient(rgba(232, 253, 235, 0.897),transparent);
    background-color: rgba(111, 170, 120, 0.904);
    width: 100vw;
    height: auto;
}


.wrapper {
    display: grid;
    grid-template-columns: repeat(auto-fit,minmax(12rem, 25rem));
    grid-gap: 3rem;
    gap: 3rem;
    /* margin-top: 20px; */
    /* margin: 20px 0; */
    justify-content: center;
}

